import React from 'react'
import { Card, Button} from 'react-bootstrap'
import { BsWhatsapp } from 'react-icons/bs'
import horizontal2 from '../assets/Comp 1/banner2_00000.jpg'
import './CardHorizontal2.css'

const CardHorizontal2 = () => {
    return (
        <Card className="horizontal2">
            <Card.Img src={horizontal2} alt="Card image" />
            <Card.ImgOverlay>
                <Card.Title>Banner de ejemplo</Card.Title>
                <Card.Text>
                    Servicios generales
                <Button style={{ height: '5rem', width: '5rem', borderRadius: '50px',}} variant="success"><BsWhatsapp /> WhatsApp</Button>
                </Card.Text>
            </Card.ImgOverlay>
        </Card>
    )
}

export default CardHorizontal2